.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

/* 🌌 NAV BAR STYLING */
nav {
  position: absolute;
  top: 0;
  width: 100%;
  display: flex;
  justify-content: flex-start;
  gap: 20px;
  padding: 10px 0;
  z-index: 10;
  margin-left: 20px;
  backdrop-filter: blur(0px); /* Add subtle blur */
  background: rgba(0, 0, 0, 0.4); /* Soft dark background */
}

/* 🌌 NAV LINKS */
nav a {
  text-decoration: none;
  font-weight: bold;
  font-family: 'Orbitron', sans-serif;
  font-size: 1rem;

  /* Gradient Text Magic */
  color: transparent;
  background: linear-gradient(90deg, #ff00ff, #00ffff, #00f0ff);
  -webkit-background-clip: text;
  background-clip: text;

  text-shadow: 0 0 8px #00ffff;

  display: inline-block;  /* 🔥 This is the fix */
  padding: 5px 8px;       /* Optional: Add some padding for space */
  transition: all 0.3s ease;
}

/* 🌌 Hover Effect for Links */
nav a:hover {
  text-shadow: 0 0 12px #ff00ff, 0 0 16px #00ffff;
  transform: scale(1.05);
}

/* 🔄 Logo Spin (default React) */
@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* 🪐 Chat Messages (for later) */
.chat-message {
  background-color: #222;
  padding: 10px;
  margin: 5px 0;
  border-radius: 5px;
}
